import React from "react";
import { withStyles } from "@material-ui/core/styles";
import Tabs from "@material-ui/core/Tabs";
import Tab from "@material-ui/core/Tab";
import { useDispatch } from 'react-redux'
import { setSortBy } from "../../reducers/sortCoursesSlice";

const StyledTabs = withStyles({
  indicator: {
    display: "flex",
    justifyContent: "center",
    backgroundColor: "transparent",
    height: "3px",
    "& > span": {
      maxWidth: 20,
      width: "100%",
      backgroundColor: "#5F80F5"
    }
  }
})((props) => <Tabs {...props} TabIndicatorProps={{ children: <span /> }} />);

const headingStyle = {
	float: 'left',
	color: `var(--heading-color)`,
  fontSize: '1.6rem',
}

const TabIndicatorBar = ({heading, tabLabels, scrolled}) => {
    const [value, setValue] = React.useState(0);
    const dispatch = useDispatch()

    const handleChange = (event, newValue) => {
        setValue(newValue);
        dispatch(setSortBy(tabLabels[newValue]));
    };

	const StyledTab = withStyles((theme) => ({
		root: {
			textTransform: "none",
			color: scrolled ? 'white' : "#95A0B0",
			fontWeight: theme.typography.fontWeightRegular,
			fontSize: theme.typography.pxToRem(15),
			marginRight: theme.spacing(1),
			minWidth: 72,
			'@media (min-width: 600px)': {
			  minWidth: 95
			},
			'@media (max-width: 950px)': {
			  minWidth: 30
			},
			"&:focus": {
			opacity: 1
			}
		},
		selected: {
			color: "#5F80F5",
			fontWeight: 600
		}
	}))((props) => <Tab disableRipple {...props} />);

    const renderTabs = () => {
        return tabLabels.map((label) => <StyledTab label={label} key={label} />);
    }

    return (
    <div className={`tab-indicator-bar ${scrolled ? 'scrolled' : ''}`}>
        {!scrolled && <h1 style={headingStyle}>{heading}</h1>}
        <StyledTabs value={value} onChange={handleChange} style={{float: 'right'}}>
            {renderTabs()}
        </StyledTabs>
    </div>
    );
}

export default TabIndicatorBar;