import React, {useEffect} from 'react';
import Container from '../../../components/Container/Container';
import { useSelector, useDispatch } from 'react-redux';
import { useParams } from 'react-router';
import axios from 'axios';

function Profile(props) {
    const userData = useSelector(state => state.userData.data);
    let searchData;
    const { username } = useParams();

    useEffect(async () => {
        if (userData.username !== username) {
            await axios.get(`/user/${username}`, {
                headers: {
                    'Authorization': `Bearer ${userData.accessToken.token} ${userData.id}`
                }
            })
            .then(res => {
                searchData = res.data.data;
                document.title = props.title || `fort | ${searchData.firstName + ' ' + searchData.lastName}`;
            })
            .catch(err => {
                const mute = err;
            });
        }
    }, [username]);

    useEffect(() => {
        if (userData.username === username)
            document.title = props.title || `fort | ${userData.firstName + ' ' + userData.lastName}`;
    }, [props.title]);

	return (
		<Container noHeader>
			
		</Container>
	)
}

export default Profile;